<template>
	<v-app app :dark="goDark">
		<v-container>
			<v-row>
				<v-col>
					<v-switch :label="`Dark Theme`" v-model="goDark"></v-switch>
				</v-col>
			</v-row>
			<acaquicktable />
		</v-container>
	</v-app>
</template>

<script>
	import UIkit from 'uikit';
	import Icons from 'uikit/dist/js/uikit-icons';
	import acaquicktable from '@/components/acaquicktable.vue';
	UIkit.use(Icons);
	export default {
		name: 'App',

		components: {
			acaquicktable,
		},
		watch: {
			goDark: function() {
				var self = this;
				if (self.goDark) {
					this.$vuetify.theme.dark = true;
				} else {
					this.$vuetify.theme.dark = false;
				}
			},
		},
		data: () => ({
			drawer: true,
			goDark: false,
		}),
	};
</script>
